// This will work without Prisma client generation in preview environments
import type { Event, Seat, ClaimedSeat } from "@/types/db"

const events: Event[] = []
const seats: Seat[] = []
const claimedSeats: ClaimedSeat[] = []

export const db = {
  event: {
    create: async (data: Omit<Event, "id" | "createdAt" | "updatedAt">) => {
      const id = `evt_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
      const event: Event = {
        id,
        ...data,
        createdAt: new Date(),
        updatedAt: new Date(),
      }
      events.push(event)
      return event
    },
    findUnique: async (where: { id: string }) => {
      return events.find((e) => e.id === where.id) || null
    },
    findMany: async (options?: any) => {
      return events.sort((a, b) => b.createdAt.getTime() - a.createdAt.getTime())
    },
    update: async (data: { where: { id: string }; data: Partial<Event> }) => {
      const event = events.find((e) => e.id === data.where.id)
      if (!event) throw new Error("Event not found")
      Object.assign(event, data.data, { updatedAt: new Date() })
      return event
    },
    findUniqueWithClaims: async (where: { id: string }) => {
      const event = events.find((e) => e.id === where.id)
      if (!event) return null
      const eventClaims = claimedSeats.filter((c) => c.eventId === where.id)
      return {
        ...event,
        claimedSeats: eventClaims,
      }
    },
    findManyWithClaims: async () => {
      return events.map((event) => {
        const eventClaims = claimedSeats.filter((c) => c.eventId === event.id)
        return {
          ...event,
          claimedSeats: eventClaims,
        }
      })
    },
  },
  claimedSeat: {
    create: async (data: Omit<ClaimedSeat, "id" | "createdAt">) => {
      const id = `seat_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
      const claim: ClaimedSeat = {
        id,
        ...data,
        createdAt: new Date(),
      }
      claimedSeats.push(claim)
      return claim
    },
    findFirst: async (where: any) => {
      return (
        claimedSeats.find(
          (s) => s.eventId === where.eventId && s.seatRow === where.seatRow && s.seatNum === where.seatNum,
        ) || null
      )
    },
  },
}
